<template>
    <div>
        <v-app>
            <v-container fluid>
                <v-navigation-drawer permanent mini-variant left app>
                    <v-list>
                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon color="#fc5531">mdi-heart-multiple</v-icon>
                            </v-list-item-icon>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon color="#fc5531">mdi-calendar-heart-outline</v-icon>
                            </v-list-item-icon>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon color="#fc5531">mdi-camera-iris</v-icon>
                            </v-list-item-icon>
                        </v-list-item>
                    </v-list>
                </v-navigation-drawer>
                <v-main>
                    <v-row justify="space-around">
                        <v-col>
                            <v-card shaped>
                                <v-card-title>我们相爱</v-card-title>
                                <v-card-text>50天</v-card-text>
                            </v-card>
                        </v-col>
                        <v-col>
                            <v-card shaped>
                                <v-card-title>我们说了晚安</v-card-title>
                                <v-card-text>50次</v-card-text>
                            </v-card>
                        </v-col>
                        <v-col>
                            <v-card shaped>
                                <v-card-title>我们说了爱你</v-card-title>
                                <v-card-text>50次</v-card-text>
                            </v-card>
                        </v-col>
                        <v-col>
                            <v-card shaped>
                                <v-card-title>我们打电话了</v-card-title>
                                <v-card-text>50次</v-card-text>
                            </v-card>
                        </v-col>
                    </v-row>
                    <v-row justify="space-around">
                        <v-col cols="6">
                            <v-card shaped>
                                <v-card-title>我们在哪里</v-card-title>
                                <v-card-text>
                                    <div id="map" style="width:100%;height:400px">地图</div>
                                </v-card-text>

                            </v-card>
                        </v-col>
                        <v-col cols="6">

                            <v-card shaped>
                                <v-card-title>我们的恋爱线</v-card-title>
                                <v-card-text>
                                    <v-timeline>
                                        <v-timeline-item>sss</v-timeline-item>
                                    </v-timeline>
                                </v-card-text>
                            </v-card>
                        </v-col>
                    </v-row>
                </v-main>
            </v-container>
        </v-app>
    </div>
</template>
<script>
export default {
    data() {
        return {
            chart: null,
            geoCoordMap: {
                '上海': [121.4648, 31.2891],
                '北京': [116.4551, 40.2539],
                '恩施': [118.8062, 31.9208],
                '宝鸡': [107.1826, 34.3433],
                '成都': [103.9526, 30.7617],
                '杭州': [119.5313, 29.8773],
                '武汉': [114.3896, 30.6628],
                '苏州': [120.6519, 31.3989],
                '西安': [109.1162, 34.2004],
                '重庆': [107.7539, 30.1904],
                '长沙': [113.0823, 28.2568],
            }
        }
    },
    methods: {
        initEcharts() {
            this.chart = this.$echarts.init(document.getElementById('map'));

            var BJData = [
                [{
                    name: '成都'
                }, {
                    name: '上海',
                    value: 95
                }],
                [{
                    name: '成都'
                }, {
                    name: '重庆',
                    value: 50
                }],
                [{
                    name: '成都'
                }, {
                    name: '长沙',
                    value: 40
                }],
                [{
                    name: '成都'
                }, {
                    name: '北京',
                    value: 30
                }],
            ];

            var planePath = 'path://M1705.06,1318.313v-89.254l-319.9-221.799l0.073-208.063c0.521-84.662-26.629-121.796-63.961-121.491c-37.332-0.305-64.482,36.829-63.961,121.491l0.073,208.063l-319.9,221.799v89.254l330.343-157.288l12.238,241.308l-134.449,92.931l0.531,42.034l175.125-42.917l175.125,42.917l0.531-42.034l-134.449-92.931l12.238-241.308L1705.06,1318.313z';
            var color = ['#00BFFF', '#48D1CC'];
            const _this = this
            var series = [];
            ['我的', '宝宝'].forEach(function (item,i) {
                series.push({
                    name: item,
                    type: 'lines',
                    zlevel: 1,
                    effect: {
                        show: true,
                        period: 6,
                        trailLength: 0.7,
                        color: '#fff',
                        symbolSize: 3
                    },
                    lineStyle: {
                        normal: {
                            color: color[i],
                            width: 0,
                            curveness: 0.2
                        }
                    },
                    data: _this.convertData(BJData,i)
                }, {
                    name: item,
                    type: 'lines',
                    zlevel: 2,
                    effect: {
                        show: true,
                        period: 6,
                        trailLength: 0,
                        symbol: planePath,
                        symbolSize: 15
                    },
                    lineStyle: {
                        normal: {
                            color: color[i],
                            width: 1,
                            opacity: 0.4,
                            curveness: 0.2
                        }
                    },
                    data: _this.convertData(BJData,i)
                }, {
                    name: item,
                    type: 'effectScatter',
                    coordinateSystem: 'geo',
                    zlevel: 2,
                    rippleEffect: {
                        brushType: 'stroke'
                    },
                    label: {
                        normal: {
                            show: true,
                            position: 'right',
                            formatter: '{b}'
                        }
                    },
                    symbolSize: function (val) {
                        return val[2] / 8;
                    },
                    itemStyle: {
                        normal: {
                            color: color[i]
                        }
                    },
                    data: BJData.map(function (dataItem) {
                        return {
                            name: dataItem[1].name,
                            value: _this.geoCoordMap[dataItem[1].name].concat([dataItem[1].value])
                        };
                    })
                });
            });

            let option = {
                backgroundColor: '#FFFFFF',
                title: {
                    text: '货运管家各地分公司地址',
                    left: 'center',
                    textStyle: {
                        color: '#808080',
                        size: '20px'
                    }
                },
                tooltip: {
                    trigger: 'item'
                },
                legend: {
                    orient: 'vertical',
                    top: 'bottom',
                    left: 'right',
                    data: ['总公司', '分公司'],
                    textStyle: {
                        color: '#fff'
                    },
                    selectedMode: 'single'
                },
                geo: {
                    map: 'china',
                    label: {
                        emphasis: {
                            show: false
                        }
                    },
                    roam: true,
                    itemStyle: {
                        normal: {
                            areaColor: '#FFFAF0',
                            borderColor: '#191970'
                        },
                        emphasis: {
                            areaColor: '#F5F5F5'
                        }
                    }
                },
                series: series
            };
            this.chart.setOption(option);
        },

        convertData(data,index) {
            const fromPlace = ['西安','恩施']
            var res = [];
            for (var i = 0; i < data.length; i++) {
                var dataItem = data[i];
                var fromCoord = this.geoCoordMap[fromPlace[index]];
                var toCoord = this.geoCoordMap[dataItem[1].name];
                if (fromCoord && toCoord) {
                    res.push([{
                        coord: fromCoord
                    }, {
                        coord: toCoord
                    }]);
                }
            }
            return res;
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.initEcharts();
        });
    }
}
</script>
<style>
.theme--light.v-card>.v-card__text {
    color: rgb(255 255 255);
}

.theme--light.v-card {
    background-color: #fc5531;
    color: rgb(255 255 255 / 100%);
}
</style>